import React, {useContext, useEffect, useState} from 'react';
import {animated, useSpring} from 'react-spring';

import {
  Assignment, School, Book, Refresh,
} from '@material-ui/icons';
import {colors, fonts} from '../theme';
import {useWindowDimensions} from '../hooks';
import DrawerItem from './drawerItem';
import {getTranslation} from '../i18n';
import {GlobalContext} from '../utils';
import MoreMenu from "./moreMenu";
import Dropdown from "./dropdown";

const app = require('../../package.json');

const width = 300;

const Drawer = (props) => {
  const {
    close, selected, onNavigate, onRefresh, student,
  } = props;
  const context = useContext(GlobalContext);
  const {user, Api, setShowAddSchool, classroom, setShowSelectPrimarySchool, isStudent} = context;
  const [selectedSchool, setSelectedSchool] = useState(classroom?.school_id);

  useEffect(() => {
    setSelectedSchool(classroom?.school_id);
  }, [classroom]);

  const setSchoolId = async (id) => {
    await Api.setClassroomSchoolId(classroom?.id, id);
  };

  const [opacityAnim, setOpacity] = useSpring(() => ({to: {opacity: 1}, from: {opacity: 0}}));
  const [animProp, setMargin] = useSpring(() => ({
    marginLeft: 0,
    from: {marginLeft: -width},
    config: {mass: 1, velocity: 0, friction: 26},
    onRest: (props) => {
      if (props.value.marginLeft === -width) close();
    },
  }));

  const animateClose = () => {
    setMargin({marginLeft: -width});
    setOpacity({opacity: 0});
  };

  const navigate = (view) => {
    onNavigate(view);
    animateClose();
  };

  const dimensions = useWindowDimensions();
  if (!user) {
    return null;
  }

  const schools = user?.schools || [{id: 0, name: '', city: ''}];
  const refresh = () => {
    onRefresh();
    animateClose();
  };

  let options = [{id: 1, title: getTranslation('addRemoveSchoolmenuItem')}];
  if (user?.schools?.length > 1) {
    options.push({id: 2, title: getTranslation('choosePrimarySchool')});
  }

  const moreThanOneSchool = schools.length > 1;
  return (
    <div>
      <animated.div
        onClick={animateClose}
        style={{
          position: 'absolute',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          backgroundColor: colors.greyedOut,
          ...opacityAnim,
        }}/>
      <animated.div style={{
        position: 'absolute',
        width,
        height: dimensions.height,
        top: 0,
        backgroundColor: colors.white,
        ...animProp,
      }}>
        <div style={{
          display: 'flex',
          backgroundColor: colors.primary,
          flexDirection: 'column',
          paddingBottom: 10,
          minHeight: 90
        }}>
          <div style={{display: 'flex', alignItems: 'center'}}>
            <img src={`https://${user.photo_url}`} style={{
              display: 'flex',
              width: '37px',
              height: '37px',
              backgroundColor: colors.white,
              borderRadius: '50px',
              margin: '14px 10px 14px 14px',
              filter: 'drop-shadow(0px 5px 5px rgba(0, 0, 0, 0.2))',
            }}/>
            <span style={{
              ...fonts.LARGE,
              fontWeight: '800',
              overflow: "hidden",
              textOverflow: "ellipsis",
              whiteSpace: "nowrap",
              width: 190,
            }}>{user.first_name} {user.last_name}</span>
            {!isStudent &&
            <MoreMenu
              onClick={({id}) => {
                switch (id) {
                  case 1:
                    setShowAddSchool(true);
                    break;
                  case 2:
                    setShowSelectPrimarySchool(true);
                    break;
                }

              }}
              options={options}
              iconStyle={{color: colors.black, transform: 'rotate(90deg)'}}
            />
            }
          </div>
          {!moreThanOneSchool && !isStudent &&
          <span
            style={{
              ...fonts.SMALL,
              width: '260px',
              fontWeight: 400,
              fontSize: 12,
              lineHeight: '1.2rem',
              overflow: 'hidden',
              textOverflow: 'ellipsis',
              whiteSpace: 'nowrap',
              marginLeft: 15,
            }}>{schools[0].name} - {schools[0].city}</span>}
          {moreThanOneSchool && schools?.map(school => (
            <div style={{
              display: 'flex',
              justifyContent: 'space-between',
              alignItems: 'center',
              margin: '0px 10px 0px 18px'
            }}>
            <span style={{
              ...fonts.SMALL,
              width: '260px',
              fontWeight: 400,
              fontSize: 12,
              lineHeight: '1.2rem',
              overflow: 'hidden',
              textOverflow: 'ellipsis',
              whiteSpace: 'nowrap',
            }}>&#x2022; &nbsp;{school?.name}{school?.city ? ` - ${school.city}` : ''}</span>
              {school.primarySchool && schools?.length > 1 &&
              <span style={{...fonts.TINY, fontWeight: '800'}}>{getTranslation('primary')}</span>}
            </div>
          ))}
        </div>
        {!moreThanOneSchool &&
        <div style={{height: 50, display: 'flex', alignItems: 'center'}}>
          <span style={{...fonts.LARGE, fontWeight: 600, padding: 15}}>{classroom?.name}</span>
        </div>
        }
        {moreThanOneSchool && <div style={{padding: 20}}>
          <Dropdown
            title={classroom?.name}
            options={schools.map(s => ({...s, name: `${s.name} - ${s.city}`}))}
            value={selectedSchool || 0}
            renderValue={(id) => {
              let school = schools.find(s => s.id === id);
              return (
                <div style={{
                  margin: 8, width: 220, overflow: 'hidden', textOverflow: 'ellipsis',
                  whiteSpace: 'nowrap'
                }}>
                  <span style={fonts.MEDIUM}>{school?.name} - {school?.city}</span>
                </div>
              );
            }}
            onChange={(e) => {
              setSchoolId(e.target.value);
              setSelectedSchool(e.target.value);
            }}/>
        </div>}

        <DrawerItem onClick={() => navigate('assignments')} selected={selected === 'assignments'}
                    iconObject={<Assignment style={{color: colors.brandColorGreen}}/>}
                    text={getTranslation('assignmentsPage')}/>
        <DrawerItem onClick={() => navigate('courseOverview')} selected={selected === 'courseOverview'}
                    iconObject={<School style={{color: colors.brandColorPink}}/>}
                    text={getTranslation('courseProgressionPage')}/>
        <DrawerItem onClick={() => navigate('courseRequirements')}
                    iconObject={<Book style={{color: colors.brandColorBlue}}/>}
                    text={getTranslation('courseRequirementsPage')}/>
        {!student
        && <>
          <DrawerItem onClick={refresh} iconObject={<Refresh style={{color: colors.textColor}}/>}
                      text={getTranslation('synchronizeMainMenu')}/>
          <div style={{borderBottom: `1px solid ${colors.borderColor}`}}/>
        </>
        }
        <span style={{
          ...fonts.TINY,
          fontSize: '10px',
          position: 'absolute',
          bottom: '6px',
          right: '6px',
          color: '#0000007f',
        }}>Version: {app?.version}</span>
      </animated.div>
    </div>

  );
};

export default Drawer;
