import {
  Checkbox,
  Divider,
  FormControl,
  InputBase,
  InputLabel,
  ListItemText,
  MenuItem,
  Select,
  withStyles,
} from '@material-ui/core';
import React from 'react';
import {colors, fonts} from '../theme';
import {getTranslation} from '../i18n';

const GreenCheckbox = withStyles({
  root: {
    color: colors.grey,
    '&$checked': {
      color: colors.green,
    },
  },
  checked: {},
})(Checkbox);

export const StudyBeeInputBase = withStyles(() => ({
  input: {
    ...fonts.MEDIUM,
    fontSize: '16px',
    height: '34px',
    alignItems: 'flex-end',
    display: 'flex',
    borderBottom: `1px solid ${colors.borderColor}`,
    padding: '6px',
    '&:focus': {
      // backgroundColor: colors.white,
    },
  },
}))(InputBase);

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
export const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
  getContentAnchorEl: null,
  autoFocus: false,
};

const allString = getTranslation('teacherAssignmentsAssessmentManyStudentIdOptionAll');
const StudybeeSelector = (
  {
    items,
    title,
    inputName,
    onSelect,
    enabled = true,
    selectedItems,
  }) => {

  const handleChange = (event) => {
    const {value} = event.target;
    let selected = [];
    if (value[value.length - 1] === 'all') {
      if (selectedItems.length !== items.length) {
        selected = items.map((s) => s.id);
      }
    } else {
      selected = value;
    }
    onSelect(selected);
  };

  return (
    <FormControl>
      <InputLabel>{title}</InputLabel>
      <Select
        disabled={!enabled}
        multiple
        value={selectedItems}
        onChange={handleChange}
        renderValue={() => (selectedItems.length === items.length ? `${allString} ${title.toLowerCase()}` : `${selectedItems.length} ${inputName} ${getTranslation('teacherAssignmentsAssessmentManyStudentTriggerMultiplyStudents')}`)}
        MenuProps={MenuProps}
        input={<StudyBeeInputBase/>}
      >
        <MenuItem key={'all'} value={'all'}>
          <GreenCheckbox checked={selectedItems.length === items.length}/>
          <ListItemText primary={allString}/>
        </MenuItem>
        <Divider/>
        {items.map((item) => (
          <MenuItem key={item.id} value={item.id}>
            <GreenCheckbox
              checked={selectedItems.find((id) => id === item.id) > -1}
            />
            <ListItemText primary={item.label}/>
          </MenuItem>
        ))}
      </Select>
    </FormControl>
  );
};
export default StudybeeSelector;
