import React, {useContext, useState} from 'react';
import {colors, fonts} from "../theme";
import {useWindowDimensions} from "../hooks";
import {getTranslation} from "../i18n";
import {GlobalContext} from "../utils";
import {Radio, withStyles} from "@material-ui/core";
import SecondaryButton from "./secondaryButton";
import PrimaryButton from "./primaryButton";


const BlueRadio = withStyles({
  root: {
    color: colors.blueText,
    '&$checked': {
      color: colors.blueText,
    },
  },
  checked: {},
})((props) => <Radio color="default" {...props} />);

const SelectPrimarySchoolDialog = () => {
  const context = useContext(GlobalContext);
  const dimensions = useWindowDimensions();
  const {Api, setShowSelectPrimarySchool, user} = context;

  const [selected, setSelected] = useState(user?.schools?.find(s => s.primarySchool));

  return (
    <div
      style={{
        position: "absolute",
        left: -dimensions.width + 360,
        right: 0,
        bottom: 0,
        top: 0,
        backgroundColor: 'transparent',
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        zIndex: "1001",
      }}
    >
      <div style={{
        width: 716,
        backgroundColor: colors.white,
        borderRadius: 6,
        position: 'absolute',
        boxShadow: '0px 5px 10px rgba(0,0,0,0.3)'
      }}>
        <div style={{
          backgroundColor: colors.primary,
          width: '100%',
          display: 'flex',
          height: 56,
          alignItems: 'center',
          borderRadius:'6px 6px 0px 0px',
        }}>
        <span style={{
          ...fonts.LARGE,
          fontWeight: 700,
          fontSize: 16,
          marginLeft: 16,
        }}>{getTranslation('choosePrimarySchool')}</span>
        </div>
        <div style={{padding: 16}}>
          {user?.schools?.map(school => (
            <div
              style={{
                height: 47,
                borderBottom: `1px solid ${colors.borderColor}`,
                display: 'flex',
                alignItems: 'center'
              }}>
              <BlueRadio checked={school.id === selected.id} onChange={() => setSelected(school)}/>
              <span style={{...fonts.MEDIUM}}>{school.name} - {school.city}</span>
            </div>
          ))}
          <div style={{
            display: 'flex',
            justifyContent: 'flex-end',
            marginTop: 20,
          }}>
            <SecondaryButton title={getTranslation('cancelBtn')} onClick={() => setShowSelectPrimarySchool(false)}/>
            <PrimaryButton
              title={getTranslation('saveBtn')}
              onClick={async () => {
                await Api.setPrimarySchool(selected.id);
                setShowSelectPrimarySchool(false);
              }}/>
          </div>
        </div>
      </div>
    </div>
  );
};
export default SelectPrimarySchoolDialog;