import {colors, fonts} from "../theme";
import {getTranslation} from "../i18n";
import {InputBase, ListItemText, MenuItem, Select, withStyles} from "@material-ui/core";
import React from "react";

const BootstrapInput = withStyles((theme) => ({
  input: {
    minHeight: 33,
    padding: 4,
    backgroundColor: 'transparent',
    borderRadius: 4,
  },
}))(InputBase);

const Dropdown = ({selectStyle, multiple=false, placeholder, title, value, onChange, changedBy, modified, renderValue, options}) => {
  return (
    <div style={{position: 'relative',}}>
      <div style={{display: 'flex', flexDirection: 'column'}}>
        <span style={{...fonts.LARGE, fontWeight: 600}}>{title}</span>
        {!!modified &&
        <span style={{
          ...fonts.SMALL,
          marginTop: 5
        }}>{getTranslation('changedBy')} {changedBy} {modified}</span>
        }
      </div>
      <div>
        <Select
          variant={'filled'}
          multiple={multiple}
          style={{width: '100%', marginTop: 10, backgroundColor:colors.greyDarker, borderRadius: 4, ...selectStyle}}
          value={value}
          onChange={onChange}
          renderValue={renderValue}
          input={<BootstrapInput/>}
        >
          {options.map((option) => (
            <MenuItem key={option.id} value={option.id}>
              <ListItemText
                primary={<span style={{...fonts.MEDIUM}}>{option.name}</span>}
                secondary={<span style={fonts.TINY}>{option.description}</span>}
              />
            </MenuItem>
          ))}
        </Select>
      </div>
      {(value?.length === 0 || !value) &&
      //  top is wrong in standalone but correct in extension so dont change!
      <span style={{...fonts.MEDIUM, position: 'absolute', left: 15, top: modified?64:40, color: colors.textColorLighter}}>
          {placeholder}
        </span>
      }
    </div>
  );
};

export default Dropdown;
