import {ButtonBase} from "@material-ui/core";
import React from "react";
import {fonts} from "../theme";
import colors from "../theme/colors";

const styles = {
  button: {
    display: 'flex',
    height: '35x',
    padding: '10px 10px 10px 10px',
    minWidth: '80px',
    borderRadius: '5px',
  }
};

const PrimaryButton = ({title, onClick, inverted = false, textStyle = {}, style = {}, disabled = false}) => {
  return (
    <ButtonBase
      disabled={disabled}
      onClick={onClick}
      style={{
        ...styles.button,
        marginLeft: '10px',
        marginRight: '10px',
        backgroundColor: inverted ? colors.primary : colors.black, ...style
      }}>
      <span style={{
        ...fonts.MEDIUM,
        fontWeight: '500',
        color: inverted ? colors.textColor : colors.white, ...textStyle
      }}>{title}</span>
    </ButtonBase>
  );
};
export default PrimaryButton;