import {
  FormControl,
  InputBase,
  InputLabel,
  ListItemText,
  MenuItem,
  Select,
  withStyles,
} from '@material-ui/core';
import React from 'react';
import {colors, fonts} from '../theme';

const BootstrapInput = withStyles((theme) => ({
  input: {
    ...fonts.MEDIUM,
    fontSize: '18px',
    minHeight: '35px',
    alignItems: 'flex-end',
    display: 'flex',
    borderBottom: `1px solid ${colors.borderColor}`,
    paddingBottom: '0px',
    marginTop: 10,
    '&:focus': {
      color: colors.green,
    },
  },
}))(InputBase);

const OptionsDropdown = (
  {
    multiple = false,
    options,
    helpText,
    value,
    handleChange,
    style = {},
    helpTextStyle = {},
    renderValueItem = null,
  }) => {
  return (
    <FormControl style={{flex: 1, display: 'flex', ...style}}>
      <InputLabel id={helpText}>
        <span
          style={{
            ...fonts.SMALL,
            color: value === '' ? colors.red : colors.green,
            ...helpTextStyle,
          }}
        >
          {helpText}
        </span>
      </InputLabel>
      <Select
        multiple={multiple}
        labelId={helpText}
        style={{width: '100%'}}
        value={value}
        onChange={handleChange}
        renderValue={(ids) => {
          if (!ids.map) {
            if (renderValueItem) {
              return renderValueItem(options.find((o) => o.id === ids));
            }
            return (
              <span
                style={{
                  ...fonts.MEDIUM,
                  overflow: 'hidden',
                  textOverflow: 'ellipsis',
                }}
              >
            {options.find((o) => o.id === ids)?.name}
          </span>);
          }
          return (
            <div style={{display: 'flex', flexWrap: 'wrap'}}>
              {ids.map(id => {
                if (renderValueItem) {
                  return renderValueItem(options.find((o) => o.id === id));
                }
                return (
                  <span
                    style={{
                      ...fonts.MEDIUM,
                      overflow: 'hidden',
                      textOverflow: 'ellipsis',
                      borderRadius: 4,
                      padding: 4,
                      backgroundColor: colors.darkYellow,
                      margin: 4,
                    }}
                  >
                {options.find((o) => o.id === id)?.name}
              </span>
                );
              })
              }</div>);
        }}
        input={<BootstrapInput/>}
      >
        {options.map((option) => (
          <MenuItem key={option.id} value={option.id}>
            <ListItemText
              primary={<span style={{...fonts.MEDIUM}}>{option.name}</span>}
              secondary={<span style={fonts.TINY}>{option.description}</span>}
            />
          </MenuItem>
        ))}
      </Select>
    </FormControl>
  );
};

export default OptionsDropdown;
