import React, {useContext, useEffect, useRef, useState} from 'react';
import {colors, fonts} from "../theme";
import {useWindowDimensions} from "../hooks";
import {GlobalContext} from "../utils";
import {Snackbar, TextField} from "@material-ui/core";
import {Alert, Autocomplete} from "@material-ui/lab";
import PrimaryButton from "./primaryButton";
import SecondaryButton from "./secondaryButton";
import {getTranslation} from "../i18n";
import closeIcon from '../images/png/close_white.png';
import Icon from "./icon";

const AddSchoolDialog = ({}) => {
  const context = useContext(GlobalContext);
  const dimensions = useWindowDimensions();
  const {Api, setShowAddSchool, user} = context;
  const [schools, setSchools] = useState();
  const [selectedSchools, setSelectedSchools] = useState();
  const [open, setOpen] = useState(false);
  const initialSelected = useRef();

  const getSchools = async () => {
    const response = await Api.getSchools(1);
    let schoolsList = response?.data?.schoolsList;
    setSchools(schoolsList);
    console.log('xxx: ', {user});
    let initial = user?.schools?.map(s => schoolsList.find(sc => sc.id === s.id));
    setSelectedSchools(initial);
    initialSelected.current = initial;
  };

  useEffect(async () => {
    getSchools();
  }, []);


  if (!selectedSchools) {
    return null;
  }

  return (
    <div
      style={{
        position: "absolute",
        left: -dimensions.width + 360,
        right: 0,
        bottom: 0,
        top: 0,
        backgroundColor: 'transparent',
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        zIndex: "1001",
      }}
    >
      <div style={{
        width: 716,
        height: 530,
        backgroundColor: colors.white,
        borderRadius: 6,
        position: 'absolute',
        boxShadow: '0px 5px 10px rgba(0,0,0,0.3)'
      }}>
        <div style={{
          backgroundColor: colors.primary,
          width: '100%',
          display: 'flex',
          height: 56,
          alignItems: 'center',
          borderRadius: '6px 6px 0px 0px',
        }}>
        <span style={{
          ...fonts.LARGE,
          fontWeight: 700,
          fontSize: 16,
          marginLeft: 16,
        }}>{getTranslation('addRemoveSchoolmenuItem')}</span>
        </div>
        <div style={{
          padding: '0px 20px 00px 20px',
          display: 'flex',
          height: 407,
        }}>
          <Autocomplete
            multiple
            disableClearable
            filterSelectedOptions
            value={selectedSchools}
            options={schools}
            getOptionLabel={(option) => option.name}
            style={{width: '100%', margin: '10px 0px 10px 0px'}}
            onChange={(_, value) => {
              setSelectedSchools(value);
            }}
            renderTags={(schools) => {
              return schools.map(school => (
                <div style={{
                  borderRadius: 4,
                  backgroundColor: colors.black,
                  padding: '4px 4px 4px 10px',
                  display: 'flex',
                  alignItems: 'center',
                  margin: '2px 10px 2px 0px',
                }}>
                  <span style={{...fonts.SMALL, color: colors.white}}>{school.name} - {school.city}</span>
                  <div
                    onClick={() => {
                      if (selectedSchools?.length > 1) {
                        setSelectedSchools(selectedSchools?.filter(s => s.id !== school.id));
                      } else {
                        setOpen(true);
                      }
                    }}>
                    <Icon
                      file={closeIcon}
                      style={{height: 12, width: 12, marginLeft: 8, marginRight: 4, cursor: 'pointer'}}
                      overrideOpacity={1}
                    />
                  </div>
                </div>
              ));

            }}
            renderInput={(params) => {
              return (
                <TextField
                  {...params}
                  style={{backgroundColor: colors.white}}
                  size={'small'}
                  variant="outlined"/>);
            }}
          />

        </div>
        <div style={{
          display: 'flex',
          justifyContent: 'flex-end',
          borderTop: `1px solid ${colors.borderOnPrimary}`,
          padding: 10,
        }}>
          <SecondaryButton title={getTranslation('cancelBtn')} onClick={() => setShowAddSchool(false)}/>
          <PrimaryButton
            disabled={!selectedSchools}
            title={getTranslation('saveBtn')}
            onClick={async () => {
              const added = selectedSchools?.filter(v => !initialSelected.current?.find(s => s.id === v.id));
              const removed = initialSelected.current?.filter(s => !selectedSchools?.find(v => v.id === s.id));

              for (const s of added) {
                await Api.addSchool(s.id);
              }
              for (const s of removed) {
                await Api.removeSchool(s.id);
              }

              setShowAddSchool(false);
            }}/>
        </div>
      </div>
      <div >
        <Snackbar
          style={{
            position: "absolute",
            top: 0,
            alignItems:'center',
            justifyContent:'center',
          }}
          open={open}
          autoHideDuration={2000}
          onClose={() => setOpen(false)}
        >
          <Alert severity={'warning'}>{getTranslation('cannotRemoveAllSchools')}</Alert>
        </Snackbar>
      </div>
    </div>
  );
};

export default AddSchoolDialog;
