import {getTranslation} from "../../i18n";
import moment from "moment";
import Dropdown from "../../components/dropdown";
import React, {useContext, useEffect, useMemo, useState} from "react";
import {colors, fonts} from "../../theme";
import debounce from "lodash.debounce";
import {GlobalContext} from "../../utils";
import PublishIcon, {PUBLISH_STATES} from "../../components/publishIcon";

const getColor = (selected) => {
  if (!selected) {
    return colors.greyDarker;
  }
  switch (selected) {
    case 2:
      return colors.ewsGreen;
    case 3:
      return colors.primary;
    case 4:
      return colors.red;
    default:
      return colors.greyDarker;
  }
};

const getTextColor = (selected) => {
  if (!selected) {
    return colors.textColor;
  }
  switch (selected) {
    case 2:
      return colors.white;
    case 3:
      return colors.white;
    case 4:
      return colors.white;
    default:
      return colors.textColor;
  }
};

const convertPublished = (published) => {
  if (published === true) {
    return PUBLISH_STATES.published;
  } else if (published === false) {
    return PUBLISH_STATES.notPublished;
  }
  return published;
};

const options = [
  {id: 1, value: '0', name: getTranslation('noStatus')},
  {id: 2, value: '10', name: getTranslation('green')},
  {id: 3, value: '20', name: getTranslation('yellow')},
  {id: 4, value: '30', name: getTranslation('red')},
];

const StatusDropdown = (
  {
    status,
    studentId,
    courseId,
    multiEdit,
    onChange,
    refresh,
  }) => {

  const context = useContext(GlobalContext);
  const {Api} = context;
  const [selected, setSelected] = useState(options.find(o => o.value === status?.status)?.id || 1);
  const [published, setPublished] = useState(convertPublished(status?.published));

  const save = useMemo(() => debounce(async (data, Api) => {
      await Api.updateStudentEWSStatus(data);
      await refresh();
    }, 1000),
    [refresh]
  );

  const saveStatus = async (status, newStatus) => {
    const saveObj = {
      status: newStatus,
      student_id: studentId,
      course_id: courseId,
      published: status?.published,
    };
    if (!multiEdit) {
      save(saveObj, Api);
    }
    if (onChange) {
      onChange(saveObj);
    }
  };

  const publish = async (status, published) => {
    const saveObj = {
      status: status?.status,
      student_id: studentId,
      course_id: courseId,
      published: published,
    };
    if (!multiEdit) {
      save(saveObj, Api);
    }
    if (onChange) {
      onChange(saveObj);
    }
  };

  useEffect(() => {
    if (multiEdit) {
      setSelected(null);
      setPublished(PUBLISH_STATES.dontChange);
    }
  }, [multiEdit]);

  useEffect(() => {
    if (!multiEdit) {
      setSelected(options.find(o => o.value === status?.status)?.id || 1);
      setPublished(convertPublished(status?.published));
    }
  }, [status, multiEdit]);

  const getPublishText = (publish) => {
    switch (publish) {
      case PUBLISH_STATES.published:
        return getTranslation('visibleToStudent');
      case PUBLISH_STATES.notPublished:
        return getTranslation('notVisibleToStudent');
      case PUBLISH_STATES.dontChange:
        return getTranslation('dontChangeVisibility');
      default:
        return null;
    }
  };

  const user = status?.user;
  const modified = status?.modified ? moment(status?.modified).format('DD/MM/YYYY') : '';
  return (
    <div style={{position: 'relative'}}>
      <Dropdown
        title={getTranslation('warninglevel')}
        placeholder={getTranslation('setWarningLevel')}
        value={multiEdit ? selected : null}
        options={options}
        selectStyle={{backgroundColor: multiEdit ? getColor(selected) : colors.greyDarker}}
        modified={multiEdit ? null : modified}
        renderValue={(id) => {
          return (
            <div style={{display: 'flex', alignItem: 'center', marginTop: 6, marginLeft: 6}}>
              <span
                style={{...fonts.MEDIUM, color: getTextColor(selected)}}>{options.find(o => o.id === id)?.name}</span>
            </div>
          );
        }}
        changedBy={`${user?.first_name} ${user?.last_name}`}
        onChange={(e => {
          setSelected(e.target.value);
          if (!multiEdit) {
            const newStatus = options.find(o => o.id === e.target.value)?.value;
            saveStatus(status, e.target.value === 1 ? '0' : newStatus);
          }
          if (onChange) {
            status.status = options.find(o => o.id === e.target.value)?.value;
            onChange(status);
          }
        })}
      />
      {!multiEdit && <div style={{marginTop: 5}}>
        <div style={{display: 'flex', alignItems: 'center'}}>
          <span style={{...fonts.MEDIUM}}>{getTranslation('currentWarningLevel')}</span>
          <div style={{backgroundColor: getColor(selected), width: 46, height: 22, borderRadius: 4, marginLeft: 4}}/>
        </div>
      </div>}
      {(!!status?.modified || multiEdit) &&
      <div
        style={{position: 'absolute', top: -6, right: 0, display: 'flex', alignItems: 'center', border: '0px solid'}}>
        <span style={{...fonts.SMALL}}>{getPublishText(published)}</span>
        <PublishIcon
          size={20}
          style={{marginTop: 0}}
          published={status?.published}
          multiEdit={multiEdit}
          onClick={(pub) => {
            setPublished(convertPublished(pub));

            if (!multiEdit) {
              publish(status, pub);
            }
            if (onChange) {
              onChange({...status, published: pub, status: options.find(o => o.id === selected)?.value});
            }
          }}/>
      </div>
      }
    </div>
  );
};

export default StatusDropdown;