import React, {useCallback, useContext, useEffect, useRef, useState} from "react";
import {colors, fonts} from "../../theme";
import Card from "../../components/card";
import {GlobalContext} from "../../utils";
import {getTranslation} from "../../i18n";
import ExtraAdjustments from "./extraAdjustments";
import InternalComments from "./internalComments";
import StatusDropdown from "./statusDropdown";

const fields = `
    name
    extraAdjustments {
      id
      name
    }
    students {
      id
      first_name
      last_name
      email
      extraAdjustments {
        id
        modified
        name
      }
      EWS {
        courseId
        courseName
        status {
          status
          published
          modified
          user {
            id
            first_name
            last_name
          }
        }
        internalComments{
          id
          modified
          comment
          course_definition_id
          student_id
          user {
            id
            first_name
            last_name
            photo_url
          }
        }
        extraAdjustments{
          id
          name
          modified
          user {
            id
            first_name
            last_name
          }
        }
      }
    }
`;

const EwsStudentCard = (props) => {
  const {student, course, multiEdit, isGrid, onChange = false} = props;
  const context = useContext(GlobalContext);
  const {Api} = context;
  const [classroom, setClassroom] = useState();
  const multiEditSaveObject = useRef({});
  const [selectedExtraAdjustments, setSelectedExtraAdjustments] = useState([]);

  const getEWSData = useCallback(async () => {
    const response = await Api.getClassroom(fields);
    setClassroom(response?.data?.classroom);
  }, [Api]);

  useEffect(() => {
    getEWSData();
  }, [student.id, multiEdit, getEWSData]);

  useEffect(() => {
    getEWSData();
  }, [getEWSData]);

  const studentEws = classroom?.students.find(s => s.id === student.id);
  const studentExtraAdjustments = studentEws?.extraAdjustments;
  const studentCourseEws = studentEws?.EWS.find((e) => e.courseId === course.id);
  const extraAdjustmentsAvailable = classroom?.extraAdjustments;
  return (
    <div>
      <span
        style={{
          ...fonts.LARGE,
          color: colors.brandColorPink,
          marginLeft: "16px",
        }}
      >
        {getTranslation("actions")}
      </span>
      <Card style={{marginTop: "16px", marginBottom: "16px"}}>
        <StatusDropdown
          refresh={getEWSData}
          status={studentCourseEws?.status || {}}
          studentId={student.id}
          courseId={course.id}
          multiEdit={multiEdit}
          onChange={(ewsStatus) => {
            multiEditSaveObject.current.ewsStatus = ewsStatus;
            if (onChange) {
              onChange(multiEditSaveObject.current);
            }
          }}
        />
        <ExtraAdjustments
          refresh={getEWSData}
          isGrid={isGrid}
          multiEdit={multiEdit}
          studentId={student.id}
          courseId={course.id}
          studentExtraAdjustments={studentExtraAdjustments}
          extraAdjustments={extraAdjustmentsAvailable}
          initialSelectedExtraAdjustments={multiEdit ? [] : studentCourseEws?.extraAdjustments}
          selectedExtraAdjustments={selectedExtraAdjustments}
          setSelectedExtraAdjustments={setSelectedExtraAdjustments}
          style={{marginTop: 20}}
          onChange={(adjustments) => {
            multiEditSaveObject.current.extraAdjustments = adjustments;
            if (onChange) {
              onChange(multiEditSaveObject.current);
            }
          }}
        />
        {!multiEdit && (
          <InternalComments courseId={course.id} studentId={student.id} refresh={getEWSData}
                            comments={studentCourseEws?.internalComments}/>
        )}
      </Card>
    </div>
  );
};

export default EwsStudentCard;
