import {colors, fonts} from "../../theme";
import React, {memo, useContext, useEffect, useMemo, useState} from "react";
import {debounce} from "lodash";
import {GlobalContext} from "../../utils";
import {getTranslation} from "../../i18n";
import EmptyText from "../../components/emptyText";
import Dropdown from "../../components/dropdown";
import moment from "moment";

const styles = {
  extraAdjustmentStyle: {
    ...fonts.MEDIUM,
    overflow: 'hidden',
    textOverflow: 'ellipsis',
    borderRadius: 4,
    padding: '4px 6px 4px 6px',
    backgroundColor: colors.darkYellow,
    margin: 2,
  },
};


const ExtraAdjustments = (
  {
    isGrid = false,
    multiEdit = false,
    onChange = null,
    studentId,
    courseId,
    style = {},
    initialSelectedExtraAdjustments = [],
    extraAdjustments = [],
    studentExtraAdjustments = [],
    refresh,
  }) => {
  const [selectedExtraAdjustments, setSelectedExtraAdjustments] = useState(initialSelectedExtraAdjustments?.map(e => e.id));
  const context = useContext(GlobalContext);
  const {Api} = context;

  const options = extraAdjustments
    .sort((a, b) => a.name.localeCompare(b.name))
    .map((extraAdjustment) => ({
      id: extraAdjustment.id,
      name: extraAdjustment.name,
      value: extraAdjustment.id,
    }));

  const saveChanges = useMemo(() => debounce(async (Api, adjustments, courseId, studentId) => {
    await Api.updateStudentExtraAdjustments2(adjustments, courseId, studentId);
    await refresh();
  }, 1000), [refresh]);

  useEffect(() => {
    if (multiEdit) {
      setSelectedExtraAdjustments([]);
    }
  }, [multiEdit]);

  useEffect(() => {
    if (!multiEdit) {
      setSelectedExtraAdjustments(initialSelectedExtraAdjustments?.map(e => e.id));
    }
  }, [initialSelectedExtraAdjustments, multiEdit]);

  let first = initialSelectedExtraAdjustments[initialSelectedExtraAdjustments.length - 1];
  return (
    <div style={style}>
      <Dropdown
        title={getTranslation('extraAdjustments')}
        placeholder={getTranslation('extraAdjustments')}
        multiple
        options={options}
        value={selectedExtraAdjustments}
        modified={first?moment(first?.modified).format('DD/MM/YYYY'):null}
        renderValue={(ids) => (
          <div style={{display: 'flex', flexWrap: 'wrap'}}>
            {ids.map(id => (<span style={{
              ...styles.extraAdjustmentStyle,
              maxWidth: isGrid ? 220 : '100%'
            }}>{options.find(o => o.id === id).name}</span>))}
          </div>
        )}
        changedBy={`${first?.user?.first_name} ${first?.user?.last_name}`}
        onChange={async (e) => {
          if (!multiEdit) {
            await saveChanges(Api, e.target.value, courseId, studentId);
          } else if (onChange) {
            onChange(e.target.value);
          }
          return setSelectedExtraAdjustments(e.target.value);
        }}
      />

      {!multiEdit && <div style={{marginTop: 20, marginBottom: 10}}><span
        style={{...fonts.LARGE, fontWeight: 600}}>{getTranslation('studentExtraAdjustments')}</span>
      </div>}
      {(studentExtraAdjustments.length === 0 && !multiEdit) &&
      <EmptyText style={{marginTop: 5}} text={getTranslation(('noExtraAdjustments'))}/>}
      {(studentExtraAdjustments.length > 0 && !multiEdit) &&
      <div style={{display: 'flex', alignItems: 'flex-start', flexWrap: 'wrap', marginTop: 5, marginBottom: 0}}>
        {studentExtraAdjustments.map(e => (
          <span
            style={{...styles.extraAdjustmentStyle, color: colors.white, backgroundColor: colors.studentExtraAdjustments}}>{e.name}</span>
        ))}
      </div>}
    </div>
  )
}

export default memo(ExtraAdjustments);